.pagination {
  display: flex;
  flex-basis: 10vh;
  justify-content: center;
  align-items: center;

  opacity: 0;
}

.pagination.active {
  opacity: 1;
}

.button.hidden {
  position: absolute;

  opacity: 0;
}

.button {
  position: relative;

  display: flex;
  justify-content: center;
  align-content: center;
  align-items: center;
  width: 2rem;
  height: 2rem;
  margin: 0 0.1rem;

  font-weight: bold;
  font-size: 0.9rem;
  line-height: 1;
  color: #626262;

  background-color: white;
  border: 2px solid #cecece;
  border-radius: 50%;
  outline: none;
  cursor: pointer;

  -moz-user-select: none;
       user-select: none;

  -webkit-touch-callout: none;
}

.button.current {
  background-color: #ffedf0;
  border: 2px solid #979797;
}

.tooltip {
  display: none;
}

.button.down .tooltip {
  position: absolute;
  bottom: 2rem;
  left: calc(0px - 25%);
  z-index: 2;

  display: inline-block;
  width: 2.5rem;
  height: 2rem;
  padding: 0.5rem;

  color: #ffffff;

  background-color: #a4a4a4;
  border-radius: 0.6rem;
}
