.search-wrapper {
  display: flex;
  flex-basis: 10vh;
  justify-content: center;
  align-items: center;

  font-weight: bold;
  font-size: 1.3rem;
  color: #2f2f2f;
}

.search-input-label {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.search-input-label .svg-inline--fa {
  position: relative;
  left: 1.4em;
}

.search-input {
  display: inline-block;
  flex: 0 1 85%;
  max-width: 85%;
  padding: 5px 5px 5px 10%;

  font-weight: inherit;
  font-size: inherit;

  border: 2px solid #979797;
  border-radius: 1rem;
}

.search-input:focus {
  color: #682763;

  background: #ffedf0;
  outline: none;
}
