.clip-list {
  --clips-count: 0;
  --position: 0;
  --drag-x: 0px;
  --clips-per-page: 4;
  --clip-width: calc(100vw / var(--clips-per-page));
  display: grid;
  grid-template-rows: auto;
  grid-template-columns: repeat(var(--clips-count),var(--clip-width));
  flex-basis: 80vh;
  align-items: center;
  width: calc(var(--clips-count) * var(--clip-width));
  margin: 0;
  padding: 0;
  justify-items: center;

  list-style: none;

  transform: translateX(calc(var(--drag-x) + -1 * var(--clips-per-page) * var(--clip-width) * var(--position)));
  cursor: grab;

  -moz-user-select: none;
       user-select: none;

  -webkit-touch-callout: none;
}

.clip-list.hover-disabled .clip {
  pointer-events: none;
}

.clip-wrapper {
  width: 100%;
  height: 90%;
  padding: 0 1rem;

  text-align: center;
}

@media (max-width: 1024px) {
  .clip-list {
    --clips-per-page: 3;
  }
}

@media (max-width: 768px) {
  .clip-list {
    --clips-per-page: 2;
  }
}

@media (max-width: 480px){
  .clip-list {
    --clips-per-page: 1;
  }
}

@media (max-height: 430px) and (orientation: landscape){
  .clip-list {
    flex-basis: 65vh;
  }

  .clip-wrapper {
    width: 80%;
  }
}

