.error {
  position: absolute;
  top: 10vh;
  z-index: 5;

  width: 100vw;

  opacity: 0;

  transition: opacity 0.5s;
}

.error.active {
  opacity: 1;
}

.error-message {
  width: 65%;
  margin: 0 auto;
  padding: 1.5rem 1rem;

  font-weight: bold;
  font-size: 2rem;
  text-align: center;
  color: #2f2f2f;
  word-break: break-word;

  background: #b0b0b0;
  border: 2px solid #979797;
  border-radius: 1rem;
}

@media (max-width: 250px) {
  .error-message {
    font-size: 1.5rem;
  }
}
