.loader {
  position: absolute;
  top: 10vh;
  z-index: -1;

  width: 100vw;

  opacity: 0;
}

.loader.active {
  z-index: 5;

  opacity: 1;
}

.dots {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  align-items: center;
  width: 20%;
  margin: 0 auto;
  padding: 1rem 0;
}

.dot {
  width: 3rem;
  height: 3rem;

  background-color: #f192b1;
  border-radius: 50%;

  animation: fade 0.8s ease-in-out alternate infinite;
}

.dot:nth-of-type(1) {
  animation-delay: -0.4s;
}

.dot:nth-of-type(2) {
  animation-delay: -0.2s;
}

@keyframes fade {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

@media (max-width: 768px) {
  .dots {
    width: 35%;
  }
}

@media (max-width: 480px) {
  .dots {
    width: 50%;
  }
}

@media (max-width: 250px) {
  .dots {
    width: 65%;
  }
}
