.clip {
  display: grid;
  grid-template: auto 1fr / 1fr;
  width: 100%;
  max-width: 500px;
  height: 75vh;
  margin: 0 auto;
  padding: 0.8rem 0;

  color: #6d6d6d;

  background-color: #fff7f8;
  border: 2px solid #979797;
  border-radius: 1rem;
  box-shadow: 0 3px 10px 0 rgba(0, 0, 0, 0.4);

  transition: background-color 0.25s;
}

.clip-channel,
.clip-view,
.clip-date {
  position: relative;

  width: 80%;
  margin: 0 auto;

  font-weight: bold;
  line-height: 1.8;
  color: #4e4e4e;
}

.clip-title {
  z-index: 2;

  grid-area: 1/1/2/2;
  align-self: center;
  margin: 0;
  overflow: hidden;

  font-size: 1.2rem;
  line-height: 1.2;
  color: #000000;
  text-overflow: ellipsis;

  background-color: rgba(249, 249, 249, 0.75);

  mix-blend-mode: luminosity;
}
.clip-title-link {
  display: inline-block;
  padding: 0.8rem;

  color: inherit;
}

.clip-thumb-wrapper {
  grid-area: 1/1/2/2;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.clip-thumb {
  width: 100%;
  height: auto;

  transition: transform ease-in 150ms;
}

.clip-title-link,
.clip-thumb {
  -webkit-user-drag: none;
}

.clip-info .svg-inline--fa {
  position: absolute;
  top: calc(50%);
  left: 0;

  transform: translateY(-50%);
}

.clip-info {
  max-width: 100%;
  margin-top: 0.8rem;
  overflow: hidden;
}

.clip-description {
  margin: 0.8rem 0;
  padding: 0.6rem;
  overflow: hidden;

  font-size: 0.9rem;
  line-height: 1.2;
  text-align: left;
  text-overflow: ellipsis;
}

.clip:hover {
  color: #ffffff;

  background-color: #d34776;
}

.clip:hover .clip-thumb {
  transform: scale(1.5);
}

.clip:hover .clip-title {
  background-color: rgba(255, 198, 208, 0.8);

  mix-blend-mode: normal;
}

.clip-title:hover {
  color: #ffffff;
  text-shadow: 2px 1px 3px rgba(5, 5, 5, 0.8);
}

.clip:hover .clip-channel,
.clip:hover .clip-view,
.clip:hover .clip-date {
  color: inherit;
}

@media (max-width: 250px) {
  .clip-channel,
  .clip-view,
  .clip-date {
    width: 95%;
  }
}

@media (max-height: 430px) and (orientation: landscape){
  .clip {
    height: 60vh;
  }
}
